.primary-content {
  margin-top: 2%;
  height: 100%;
  width: 100%;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.resp-textbox {
  position: relative;
  width: 300px;
}
.resp-textbox > input[type="text"] {
  width: 100%;
  padding: 5px;
  font-size: 20px;
  background-color: #0003;
  border: none;
  border-radius: 5px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25) inset;
  color: #fff;
}
.resp-textbox > input[type="text"]:focus {
  outline: none;
  box-shadow: 325px 2px 5px rgba(0, 0, 0, 0.25) inset;
}
.resp-textbox > input[type="text"]::placeholder {
  color: #fff8;
}
